<template>
  <div class="btn-wrapper">
    <van-button @click="login" type="primary" block></van-button>
  </div>
</template>
<script>
import {Button, } from 'vant'
import axios from 'axios'
import {authApi} from '../config'

axios.defaults.withCredentials = true

export default {
  components: {
    [Button.name]: Button,
  },
  methods: {
    login() { 
      axios.get(authApi, {
        params: {
          uid: 'admin'
        }
      })
      .then((response) => {
        const { userid, JSESSIONID } = response.data.body
        localStorage.setItem('auth', userid)
        localStorage.setItem('sessionId', JSESSIONID)
        this.$router.push(this.$route.query.redirect || '/')
      })
      .catch(function (error) {
        console.log(error);
      });
    }
  }
}
</script>
<style lang="scss" scoped>
  .btn-wrapper {
    display: flex;
    height: calc(100vh - 16px);
    margin: 8px;
    align-items: center;
  }
</style>