<template>
  <li>
    <div class="card-box">
      <div class="box-body">
        <van-icon name="clock-o" class="clock" />
        <span class="time">{{data['procIns.startTime'] | dateFormat}}</span>
        <span v-if="data['procIns.endTime']" class="line">-</span>
        <span v-if="data['procIns.endTime']" class="time">{{data['procIns.endTime'] | dateFormat}}</span>
        <div class="info-wrapper">
          <div class="left">
            <h3 class="title">{{data.vars.title}}</h3>
            <h4 class="flow">{{data['procDef.name']}}</h4>
            <ul class="info">
              <li>
                <h4></h4>
                <div class="box">
                  {{data.vars.userName}}
                </div>
              </li>
              <li>
                <h4></h4>
                <div class="box">
                  <Status :status="data['procIns.status']" />
                </div>
              </li>
            </ul>
          </div>
          <div class="right">
            <van-button icon="arrow" plain size="small" type="danger" :to="{path: `/detail/${data['procIns.processInstanceId']}`}"></van-button>
            <van-button v-if="data['procIns.status']=='[]' && rate.flag === '0'" icon="star-o" plain size="small" type="default" class="btn-commit" @click="onRate(rate.id, data.vars.title)"></van-button>
            <span v-else-if="data['procIns.status']=='[]' && rate.flag === '1'" class="rate-done"></span>
          </div>
        </div>
      </div>
      <!--
      <div class="box-footer">
        <div class="btn-group">
          <router-link to="/detail"><van-button plain size="small" type="info"></van-button></router-link>
        </div>
      </div>
      -->
    </div>
  </li>
</template>
<script>
import {Button, Icon} from 'vant'
import {get} from 'lodash'
import dayjs from 'dayjs'
import axios from 'axios'
import Status from './Status'
import { apiHost } from '../config'

export default {
  props: ['data'],
  components: {
    [Button.name]: Button,
    [Icon.name]: Icon,
    Status
  },
  data() {
    return {
      rate: {
        flag: 0,
        id: 0,
      },
    }
  },
  mounted() {
    this.getRate(this.data['procIns.processInstanceId'])
    console.log(this.data['procIns.processInstanceId'],'----->', this.data)
  },
  filters: {
    getKey(key, data) {
      return get(data, key, '')
    },
    dateFormat(timestamp){
      return dayjs(timestamp).format('YYYY-MM-DD HH:mm:ss')
    }
  },
  methods: {
    onRate(id, title) {
      this.$emit('onRate', id, title)
    },
    onGo(id) {
      this.$router.push('/detail/' + id)
    },
    getRate(id) {
      const sessionId = localStorage.getItem('sessionId')
      axios.get(`${apiHost}/repair/a/wechat/api/evaluate/getEvaluateInfo;JSESSIONID=${sessionId}`, {
        params: {
          procInstId: id
        }
      }).then(res => {
        console.log('++++++',res.data.body.evaluate)
        if(res.data.body.evaluate) {
          this.rate = {
            id: res.data.body.evaluate.id,
            flag: res.data.body.evaluate.orderFlag,
            rate: res.data.body.evaluate.grade,
            commit: res.data.body.evaluate.content
          }
          console.log('=====', this.rate)
        }
        
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>
