<template>
  <div>
    <NavBar title="" />
    <Loading v-if="loading" />
    <div v-else class="content">
      <ul v-if="dataList" class="list">
        <Card :data="item" v-for="item in dataList" :key="item.title" @onRate="onRate" />
      </ul>
      <van-empty v-else description="" />
      <div class="page" v-if="dataList"><van-pagination @change="pageChange" v-model="page.pageNo" :page-count="parseInt((page.pageCount + page.pageSize - 1) / page.pageSize, 10)" mode="simple" /></div>
    </div>
    <div class="btn-add">
      <router-link to="/add">
        <van-icon name="plus" />
        <div></div>
      </router-link>
    </div>
    <van-action-sheet v-model="showRate" title="">
      <div class="popup-content">
        <h4 class="rate-title">{{order.title}}</h4>
        <div class="form">
          <van-cell-group>
            <van-field name="rate" label="">
              <template #input>
                <van-rate
                  count="3"
                  v-model="rateValue"
                  :size="25"
                  color="#3ca2e0"
                  void-icon="star"
                  void-color="#eee"
                />
                <span class="rate-text">{{ rateValue | rateText }}</span>
              </template>
            </van-field>
            <van-field v-model="rateCommit" label="" type="textarea" placeholder="" />
          </van-cell-group>
          <div class="btn-group">
            <van-button type="info" @click="submitRate"></van-button>
            <van-button plain type="danger" @click="showRate = false"></van-button>
          </div>
        </div>
      </div>
    </van-action-sheet>
  </div>
</template>
<script>
import {Empty, Pagination, Button, Icon, ActionSheet, Rate, CellGroup, Field, Toast} from 'vant'
import Card from '@/components/Card'
import NavBar from '@/components/NavBar'
import axios from 'axios'
import Loading from '../components/Loading'
import {apiHost}from '../config'
import { auth } from '../auth'
export default {
  props: ['updateIsAdmin'],
  components: {
    [Empty.name]: Empty,
    [Button.name]: Button,
    [Icon.name]: Icon,
    [NavBar.name]: NavBar,
    [Pagination.name]: Pagination,
    [ActionSheet.name]: ActionSheet,
    [Rate.name]: Rate,
    [CellGroup.name]: CellGroup,
    [Field.name]: Field,
    Loading,
    Card,
    NavBar,
  },
  data() {
    return {
      loading: true,
      rateValue: 3,
      rateCommit: '',
      showRate: false,
      dataList: null,
      order: {},
      page: {},
      id: null,
    }
  },
  mounted () {
    const authId = localStorage.getItem('auth')
    auth(authId)
    this.getList(1)
    this.getUser()
  },
  methods: {
    onRate(id, title) {
      this.order = {id, title}
      this.id = id
      this.rateValue = 3
      this.rateCommit = ''
      this.showRate = true
    },
    getList(pageNo = 1) {
      const sessionId = localStorage.getItem('sessionId')
      axios
      .get(`${apiHost}/repair/a/wechat/api/task/myApplyed;JSESSIONID=${sessionId}`, {
        params: {
          pageNo
        }
      })
      .then(response => {
        this.loading = false;
        this.dataList = response.data.body.page.list;
        this.page = {
          pageNo: response.data.body.page.pageNo,
          pageCount: response.data.body.page.count,
          pageSize: response.data.body.page.pageSize
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getUser() {
      const uid = localStorage.getItem('auth')
      axios.get(`${apiHost}/repair/a/wechat/api/userById?uid=${uid}`, {
        withCredentials:true
      }).then(res => {
        this.user = res.data.body.user
        localStorage.setItem('user', JSON.stringify(res.data.body.user));
        localStorage.setItem('admin', res.data.body.user.roleEnNames !== 'commonUser');
        this.$emit('updateIsAdmin', res.data.body.user.roleEnNames !== 'commonUser')
      }).catch(err => {
        console.log(err)
      })
    },
    submitRate() {
      const data = {
        id: this.id,
        grade: this.rateValue,
        content: this.rateCommit
      };
      const sessionId = localStorage.getItem('sessionId')

      axios.post(`${apiHost}/repair/a/wechat/api/evaluate/save;JSESSIONID=${sessionId}`, data).then(res => {
        if(res.data.success){
          this.showRate = false
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    pageChange(page) {
      this.getList(page)
    }
  },
  filters: {
    rateText: function(value) {
      const rateText = ['', '', ''];
      return rateText[value - 1];
    }
  }
}
</script>
