<template>
  <div>
    <div class="content">
      <van-tabs v-model="active" type="card" color="#c9333e" @click="onClick">
        <van-tab title="">
          <Results type="todo" />
        </van-tab>
        <van-tab title="">
          <Results type="done" />
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>
<script>
import {Tabs, Tab, Empty} from 'vant'
import axios from 'axios'
import Results from './Results'
import { apiHost } from '../config';
import {auth} from '../auth'
export default {
  data() {
    return {
      todoList: null,
      todoPage: null,
      active: 0,
    };
  },
  components: {
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [Empty.name]: Empty,
    Results,
  },
  mounted() {
    const authId = localStorage.getItem('auth')
    auth(authId)
    const tab = parseInt(this.$route.query.tab || 0, 10);
    console.log(tab, '00',this.$route)
    this.active = tab
    this.getUser()
  },
  methods: {
    onClick(name, title){
      console.log(name, title)
      this.$router.push('/list?tab=' + name)
    },
    getUser() {
      const uid = localStorage.getItem('auth')
      axios.get(`${apiHost}/repair/a/wechat/api/userById?uid=${uid}`, {
        withCredentials:true
      }).then(res => {
        this.user = res.data.body.user
        localStorage.setItem('user', JSON.stringify(res.data.body.user));
        localStorage.setItem('admin', res.data.body.user.roleEnNames !== 'commonUser');
        this.$emit('updateIsAdmin', res.data.body.user.roleEnNames !== 'commonUser')
      }).catch(err => {
        console.log(err)
      })
    },
  }
};
</script>
<style lang="scss">
  .content {
    margin: 8px 0;
  }
  .van-tabs__nav {
    margin-left: 0;
    margin-right: 0;
  }
  .van-tabs__nav--card {
    margin: 0 9px;
  }
</style>