<template>
  <div v-if="statusMapping == 0" class="failed">
    <van-icon name="close" />
    <span></span>
  </div>
  <div v-else-if="statusMapping == 4" class="success">
    <van-icon name="passed" />
    <span>{{ statusMapping | statusText }}</span>
  </div>
  <div v-else class="inprocess">
    <van-icon name="clock-o" />
    <span>{{ statusMapping | statusText }}</span>
  </div>
</template>
<script>
import {Icon} from 'vant'

const statusArray = ['[]', '[] :', '[] :', '[] :', '[]'];
const statusShowArray = ['', '', '', '', ''];
export default {
  props: ['status'],
  components: {
    [Icon.name]: Icon
  },
  computed: {
    statusMapping: function() {
      const statusArr = this.status.indexOf('[') !== -1 ? statusArray : statusShowArray
      return statusArr.indexOf(this.status)
    }
  },
  filters: {
    statusText(status) {
      return statusShowArray[status];
    }
  }
}
</script>
<style lang="scss" scoped>
  i {
    vertical-align: middle;
    font-size: 16px;
    margin-right: 5px;
  }
  span {
    vertical-align: middle;
  }
  .success {
    color: #07c160;
    i {
      color: inherit;
    }
  }
  .failed {
    color: #ee0a24;
    i {
      color: inherit;
    }
  }
  .inprocess {
    color: #1989fa;
  }
</style>