<template>
  <Loading v-if="loading" />
  <div v-else>
    <ul v-if="data" class="list">
      <Task :data="item" v-for="item in data" :key="item.id" :getList="getList" />
    </ul>
    <van-empty v-else description="" />
    <div class="page" v-if="data">
      <van-pagination @change="pageChange" v-model="page.pageNo" :page-count="parseInt((page.count + page.pageSize - 1) / page.pageSize, 10)" mode="simple" />
    </div>
  </div>
</template>
<script>
import {Empty, Pagination} from 'vant'
import axios from 'axios'
import Task from '@/components/Task'
import Loading from '@/components/Loading'
import {apiHost} from '../config'
import { auth } from '../auth'
export default {
  props: ['type'],
  components: {
    [Empty.name]: Empty,
    [Pagination.name]: Pagination,
    Loading,
    Task,
  },
  data() {
    return {
      page: {},
      loading: true,
    }
  },
  mounted() {
    this.getList(this.type)
  },
  methods: {
    getList(type, pageNo=1) {
      const sessionId = localStorage.getItem('sessionId')
      const todoApi = `/repair/a/wechat/api/task/todo;JSESSIONID=${sessionId}`;
      const doneApi = '/repair/a/wechat/api/task/historic';
      const apiPath =  type === 'todo' ? todoApi : doneApi;

      axios.get(`${apiHost}${apiPath}`, {
        params: {
          pageNo
        },
        withCredentials:true
      }).then(res => {
        this.data = res.data.body.page.list
        this.page = {
          pageNo: res.data.body.page.pageNo,
          count: res.data.body.page.count,
          pageSize: res.data.body.page.pageSize,
        }
        this.loading = false
      }).catch(err => {
        console.log(err)
      })
    },
    pageChange(page) {
      this.getList(this.type, page)
    },
  },
  
}
</script>
<style lang="scss" scoped>
  .list {
    margin-left: 8px;
    margin-right: 8px;
  }
  .page {
    margin: 8px;
  }
</style>