<template>
  <li>
    <div class="card-box">
      <div class="box-body">
        <van-icon name="clock-o" class="clock" />
        <span class="time">{{data['task.createTime'] | dateFormat}}</span>
        <span v-if="data['procIns.endTime']" class="line">-</span>
        <span v-if="data['procIns.endTime']" class="time">{{data['procIns.endTime'] | dateFormat}}</span>
        <div class="info-wrapper">
          <div class="left">
            <h3 class="title">{{data.vars.title}}</h3>
            <ul class="info">
              <li>
                <h4></h4>
                <div class="box">
                  {{data.vars.userName}}
                </div>
              </li>
              <li>
                <h4></h4>
                <div class="box">
                  <Status :status="data['task.name'] || data.status" />
                </div>
              </li>
            </ul>
          </div>
          <div class="right">
            <van-button v-if="data.status == 'todo'" icon="arrow" plain size="small" type="danger" :to="{path: `/add/${data['task.processInstanceId']}`}"></van-button>
            <van-button v-else-if="data.status == 'claim'" icon="arrow" plain size="small" type="danger" @click="cliam(data['task.id'])"></van-button>
            <van-button v-else icon="arrow" plain size="small" type="danger" :to="{path: `/detail/${data['task.processInstanceId']}`}"></van-button>
            <van-button v-if="data.status==2 && !data.rate" icon="star-o" plain size="small" type="default" class="btn-commit" @click="onRate(data.id)"></van-button>
          </div>
        </div>
      </div>
      <!--
      <div class="box-footer">
        <div class="btn-group">
          <router-link to="/detail"><van-button plain size="small" type="info"></van-button></router-link>
        </div>
      </div>
      -->
    </div>
  </li>
</template>
<script>
import {Button, Icon, Toast} from 'vant'
import {get} from 'lodash'
import dayjs from 'dayjs'
import axios from 'axios'
import {apiHost} from '../config'
import Status from './Status'

export default {
  props: ['data', 'getList'],
  components: {
    [Button.name]: Button,
    [Icon.name]: Icon,
    Status
  },
  data() {
    return {

    }
  },
  mounted() {
  },
  filters: {
    getKey(key, data) {
      return get(data, key, '')
    },
    dateFormat(timestamp){
      return dayjs(timestamp).format('YYYY-MM-DD HH:mm:ss')
    }
  },
  methods: {
    onRate(id) {
      this.$emit('onRate', id)
    },
    onGo(id) {
      this.$router.push('/detail/' + id)
    },
    cliam(taskId) {
      const sessionId = localStorage.getItem('sessionId')
      axios.get(`${apiHost}/repair/a/wechat/api/task/claim;JSESSIONID=${sessionId}`, {
        params: {
          taskId
        }
      }).then(res => {
        Toast('');
        this.getList('todo')
      }).catch(err => console.log(err))
    }
  }
}
</script>
