import axios from 'axios'
import { authApi } from './config'

const auth = (uid) => {
  axios.get(authApi, {
      params: {
        uid
      }
    })
    .then((response) => {
      const { userid, JSESSIONID } = response.data.body
      localStorage.setItem('auth', userid)
      localStorage.setItem('sessionId', JSESSIONID)
    })
    .catch(function(error) {
      console.log(error);
    });
}

export {
  auth
}
