  <div class="nav-bar">
    <div class="back" @click="onBack">
      <van-icon name="arrow-left" v-if="back" />
      <span v-if="back"></span>
    </div>
    <h2 class="title-bar" :style="{textAlign: align }">{{title}}</h2>
    <div class="more"></div>
  </div>
</template>
<script>
import {Icon} from 'vant'
export default {
  data() {
    return {
    }
  },
  props: {
    title: String,
    align: String,
    back: Boolean,
  },
  components: {
    [Icon.name]: Icon
  },
  methods: {
    onBack() {
      if(this.back) {
        this.$router.back(-1)
      }
    }
  }
}
</script>
