<template>
  <div class="wrapper">
    <NavBar v-if="id" back title="" />
    <NavBar v-else back title="" />
    <div class="form">
      <van-form @submit="onSubmit">
        <Panel title="">
          <van-cell-group>
            <van-field readonly v-model="user.name" name="userName" label="" placeholder="" />
            <van-field readonly v-model="user.loginName" name="idNumber" label="" placeholder="" />
            <van-field readonly v-model="user.office.name" name="depart" label="" placeholder="" />
            <van-field :readonly="readonly" v-model="user.mobile" name="phone" label="" placeholder="" />
            <van-field
              readonly
              clickable
              label=""
              name="schoolArea"
              :value="schoolArea.text"
              placeholder=""
              right-icon="arrow-down"
              @click="showSchoolPicker = true"
              :rules="[{ required: true, message: '' }]"
            />
            <van-popup v-if="!readonly" v-model="showSchoolPicker" round position="bottom">
              <van-picker
                show-toolbar
                :columns="schools"
                @cancel="showSchoolPicker = false"
                @confirm="onSchoolConfirm"
              />
            </van-popup>
            <van-field
              readonly
              clickable
              label=""
              :value="area.text"
              name="area"
              placeholder=""
              right-icon="arrow-down"
              @click="showAreaPicker = true"
              v-if="areas.length > 0 || area.text"
              :rules="[{ required: true, message: '' }]"
            />
            <van-popup v-if="!readonly" v-model="showAreaPicker" round position="bottom">
              <van-picker
                show-toolbar
                :columns="areas"
                @cancel="showAreaPicker = false"
                @confirm="onAreaConfirm"
              />
            </van-popup>
            <van-field
              :readonly="readonly" 
              v-model="title" 
              name="title" 
              label="" 
              placeholder=""
              :rules="[{ required: true, message: '' }]"
            />
            <van-field
              :readonly="readonly" 
              v-model="description" 
              name="description" 
              label="" 
              type="textarea" 
              placeholder=""
              :rules="[{ required: true, message: '' }]"
            />
            <van-field
              :readonly="readonly" 
              v-model="location" 
              name="location" 
              label="" 
              placeholder=""
              :rules="[{ required: true, message: '' }]"
            />
            <van-field name="userAttachment" label="" placeholder="" :rules="[{ required: true, message: '' }]">
              <template #input>
                <van-uploader
                  v-model="attachment"
                  multiple
                  :before-read="beforeRead"
                  :after-read="afterRead"
                  @oversize="onOversize"
                  max-count="3"
                  :max-size="10 * 1024 * 1024"
                  :show-upload="!readonly"
                  :deletable="!readonly"
                  :before-delete="deleteFj"
                  :preview-image="true"
                />
              </template>
            </van-field>
          </van-cell-group>
        </Panel>
        <Panel title="" v-if="isFirstStep || isSecondStep || isThirdStep">  
          <van-cell-group>
            <van-field name="approveStatus" label="">
              <template #input>
                <van-radio-group v-model="approveStatus" direction="horizontal" :disabled="isSecondStep || isThirdStep">
                  <van-radio name="1"></van-radio>
                  <van-radio name="2"></van-radio>
                </van-radio-group>
              </template>
            </van-field>
            <van-field
              readonly
              clickable
              label=""
              name="assignClass"
              :value="assignClass.text"
              v-if="approveStatus == 1"
              placeholder=""
              right-icon="arrow-down"
              @click="showClassPicker = true"
              :rules="[{ required: true, message: '' }]"
            />
            <van-popup v-if="isFirstStep" v-model="showClassPicker" round position="bottom">
              <van-picker
                show-toolbar
                :columns="classGroup"
                @cancel="showClassPicker = false"
                @confirm="onClassConfirm"
              />
            </van-popup>
            <van-field :readonly="isSecondStep || isThirdStep" v-model="leader.name" v-if="approveStatus == 1" name="classLeader" label="" placeholder="" />
            <van-field v-if="isFirstStep" v-model="approve" name="approve" label="" type="textarea" placeholder="" />
            
          </van-cell-group>
        </Panel>
        <Panel v-if="isSecondStep || isThirdStep" title="">
          <van-cell-group>
            <van-field name="approveClassStatus" label="">
              <template #input>
                <van-radio-group v-model="approveClassStatus" direction="horizontal" disabled>
                  <van-radio name="1"></van-radio>
                  <van-radio name="2"></van-radio>
                </van-radio-group>
              </template>
            </van-field>
            <van-field
              readonly
              clickable
              label=""
              name="assignMember"
              :value="assignMember.text"
              placeholder=""
              right-icon="arrow-down"
              @click="showMemberPicker = true"
              :rules="[{ required: true, message: '' }]"
            />
            <van-popup v-if="isSecondStep" v-model="showMemberPicker" round position="bottom">
              <van-picker
                show-toolbar
                :columns="memberGroup"
                @cancel="showMemberPicker = false"
                @confirm="onMemberConfirm"
              />
            </van-popup>
            <van-field v-if="isSecondStep" v-model="approve" name="approve" label="" type="textarea" placeholder="" />
          </van-cell-group>
        </Panel>
        <Panel v-if="isThirdStep" title="">
          <van-cell-group>
            <van-field name="classAttachment" label="" placeholder="">
              <template #input>
                <van-uploader
                  v-model="classAttachment"
                  multiple
                  :before-read="beforeRead"
                  :after-read="afterRead"
                  @oversize="onOversize"
                  max-count="3"
                  :max-size="10 * 1024 * 1024"
                  :before-delete="deleteFj2"
                  :rules="[{ required: true, message: '' }]"
                />
              </template>
            </van-field>
            <van-field v-model="approve" name="approve" label="" type="textarea" placeholder="" />
          </van-cell-group>
        </Panel>
        <div class="btn-group">
          <van-button type="danger" native-type="submit"></van-button>
          <van-button plain type="danger" @click="onBack" native-type="button"></van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>
<script>
import {CellGroup, Field, RadioGroup, Radio, Uploader, Picker, Popup, Button, Form, Toast} from 'vant'
import NavBar from '@/components/NavBar'
import Panel from '@/components/Panel'
import {detailInfoFormat, imgUrl} from '@/helper/dataFormat'
import axios from 'axios'
import {apiHost} from '../config'
import { auth } from '../auth'
import Compressor from 'compressorjs';

const sessionId = localStorage.getItem('sessionId')

export default {
  components: {
    [CellGroup.name]: CellGroup,
    [Field.name]: Field,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Uploader.name]: Uploader,
    [Picker.name]: Picker,
    [Popup.name]: Popup,
    [Button.name]: Button,
    [Form.name]: Form,
    NavBar,
    Panel,
  },
  data() {
    return {
      id: null,
      user: {
        office: {
        }
      },
      isAdmin: false,
      classMonitor: '',
      approve: '',
      title: '',
      description: '',
      location: '',
      assignClass: '',
      attachment: [],
      classAttachment: [],
      approveStatus: '1',
      approveClassStatus: '1',
      schoolArea: {},
      area: {},
      asignClass: {},
      showSchoolPicker: false,
      showAreaPicker: false,
      showClassPicker: false,
      showMemberPicker: false,
      schools: [],
      areas: [],
      classGroup: [],
      memberGroup: [],
      schoolsData: null,
      fj: [],
      fj1: [],
      fj2: [],
      act: {},
      leader: {},
      approveClass: '',
      assignMember: {}
    }
  },
  mounted() {
    const id = this.$route.params.id
    const authId = localStorage.getItem('auth')
    auth(authId)
    
    this.getSchools()
    this.isAdmin = !!id
    this.isAdmin = localStorage.getItem('admin')
    this.user = JSON.parse(localStorage.getItem('user'))

    if(id) {
      this.status = 2
      this.getTaskData(id)
    }else{
      this.getRpr()
    }

  },
  computed: {
    readonly: function () {
      return this.act.taskDefKey !== 'adminPreviewAudit' && this.act.taskDefKey !== undefined
    },
    isFirstStep: function () {
      return this.act.taskDefKey === 'adminPreviewAudit'
    },
    isSecondStep: function () {
      return this.act.taskDefKey === 'classgroupAudit'
    },
    isThirdStep: function () {
      return this.act.taskDefKey === 'classuserAudit'
    }
  },
  methods: {
    getSchools() {
      axios.get(`${apiHost}/repair/a/wechat/api/campus/getCamplus;JSESSIONID=${sessionId}`).then(res => {
        const schoolsData = res.data.body.camplusList;
        this.schoolsData = schoolsData;
        this.schools = schoolsData.map(item => item && ({text: item.name, value: item.id}))
      }).catch(err => console.log(err))
    },
    getAreas(school) {
      axios.get(`${apiHost}/repair/a/wechat/api/campus/getChildren;JSESSIONID=${sessionId}`, {
        params: {
          parentId: school
        }
      }).then(res => {
        const areasData = res.data;
        this.areas = areasData.map(item => item && ({text: item.name, value: item.id}))
      }).catch(err => console.log(err))
    },
    getClassGroup(camplusId) {
      axios.get(`${apiHost}/repair/a/wechat/api/campusClass/data;JSESSIONID=${sessionId}`, {
        params: {
          camplusId
        }
      }).then(res => {
        const classGroupData = res.data.body.page.list;
        this.classGroup = classGroupData.map(item => ({text: item.classname, value: item.id, classId: item.classid}))
      }).catch(err => console.log(err))
    },
    getClassLeader(id) {
      axios.get(`${apiHost}/repair/a/wechat/api/classuser/getLeaderById;JSESSIONID=${sessionId}`, {
        params: {
          id
        }
      }).then(res => {
        const leader = res.data.body.leader;
        this.leader = {
          name: leader.userName,
          userNo: leader.userNo,
          phone: leader.phone,
          classgroup: {
            id: leader.classgroup.id
          }
        }
      }).catch(err => console.log(err))
    },
    getMemberGroup(groupId) {
      axios.get(`${apiHost}/repair/a/wechat/api/classuser/getClassUserByGroupId;JSESSIONID=${sessionId}`, {
        params: {
          groupId
        }
      }).then(res => {
        const memberGroupData = res.data.body.userList;
        this.memberGroup = memberGroupData.map(item => ({text: item.userName, value: item.userNo, phone: item.phone, id: item.id}))
      }).catch(err => console.log(err))
    },
    onSchoolConfirm(value) {
      if(value !== this.schoolArea) {
        this.area = '';
      }
      
      this.schoolArea = value;
      this.showSchoolPicker = false;
      this.getAreas(value.value)
    },
    onAreaConfirm(value) {
      this.area = value;
      this.showAreaPicker = false;
    },
    onClassConfirm(value) {
      this.assignClass = value;
      this.showClassPicker = false;
      this.getClassLeader(value.value)
    },
    onMemberConfirm(value) {
      this.assignMember = value;
      this.showMemberPicker = false;
    },
    onOversize(file) {
      Toast(' 10M');
    },
    deleteFj(fileObj, detail) {
      const arr = [...this.fj1]
      arr.splice(detail.index,1)
      this.fj1 = arr
      return true;
    },
    deleteFj2(fileObj, detail) {
      const arr = [...this.fj2]
      arr.splice(detail.index,1)
      this.fj2 = arr
      return true;
    },
    beforeRead(file) {
      /*
      if (file.type !== 'image/jpeg') {
        Toast(' jpg ');
        return false;
      }
      */
      return true;
    },
    afterRead(file) {
      const _this = this;
      const sessionId = localStorage.getItem('sessionId')
      file.status = 'uploading';
      file.message = '...';
      new Compressor(file.file, {
          quality: 0.6,
          success(result) {
            const formData = new FormData();
            file.file = result
            formData.append('file', result, result.name);

            axios.post(apiHost + `/repair/a/wechat/api/file/webupload/upload;JSESSIONID=${sessionId}`, formData, {
              headers: {
                'Content-Type': 'multipart/form-data',
              },
            }).then(res => {
              let str = res.data.body.url;

              if(!res.data.body.url) {
                file.status = 'failed';
                file.message = '';
              } else {
                file.status = null;
                file.message = null;
              }

              if(_this.isThirdStep) {
                _this.fj2.push({url: str});
              } else {
                _this.fj1.push({url: str});//
              }

              console.log(_this.fj1, '@', _this.fj2)

            });
          },
          error(err) {
            console.log(err.message);
          },
        });
    },
    onSubmit(values) {
      const { userName, idNumber, description, title, location, phone, approveStatus, approve, userAttachment } = values
      const fj1 = this.fj1

      const data = {
        content: description,
        rpaTtile: title,
        bxr: {
          id: this.user.id,
          name: userName
        },
        userNo: idNumber,
        userName: userName,
        depart: {
          id: this.user.office.id,
          name: this.user.office.name
        },
        mobile: phone,
        campus: {
          id: this.schoolArea.value,
          name: this.schoolArea.text
        },
        cate: {
          id: this.area.value,
          name: this.area.text
        },
        address: location,
        fj: fj1.map(item => item.url).join('|'),
        act: this.act
      };

      if(this.id) {
        data.id = this.dataId
        data.rpaNo = this.id
        data.act = {
          taskDefKey: this.act.taskDefKey,
          taskId: this.act.taskId,
          procInsId: this.act.procInsId,
          flag: approveStatus == 1 ? 'yes' : 'no',
          comment: approve
        }
        data.rpaCls = {
          id: this.assignClass.value,
          classname: this.assignClass.text,
          classid: this.assignClass.classId,
        }
        data.leader = this.leader.name
        data.rpaLeaderNo = this.leader.userNo
        data.classGroupId = this.leader.classgroup.id
      }

      if(this.isThirdStep) {
        data.rpaRepairfj = this.fj2.map(item => item.url).join('|')
      }

      if(this.isSecondStep || this.isThirdStep) {
        data.rpaUser = {
          id: this.assignMember.id,
          userNo: this.assignMember.value,
          userName: this.assignMember.text,
        }
        data.rpaUserNo = this.assignMember.value
      }

      const approvPath = this.id ? 'auditSave' : 'save'

console.log(this.isThirdStep, 'step', this.fj2)
console.log('*****', data)

      axios.post(`${apiHost}/repair/a/wechat/api/rprorder/${approvPath};JSESSIONID=${sessionId}`, data).then(res => {
        Toast(res.data.msg)
        setTimeout(() => {
          this.$router.push(this.id ? '/list' : '/')
        }, 1000)
      }).catch(err => {
        console.log(err)
      })
    },
    getRpr() {
      axios.get(`${apiHost}/repair/a/wechat/api/rprorder/getRprOrder;JSESSIONID=${sessionId}`).then(res => {
        const d = res.data.body.rprOrder
        this.user = {
          name: d.userName,
          loginName: d.userNo,
          office: {
            name: d.depart.name,
            id: d.depart.id
          },
          id: d.bxr.id,
          mobile: d.mobile
        }
        this.act = {
          procDefId: d.act.procDefId,
          procDefKey: d.act.procDefKey,
        }
      }).catch(err => console.log(err))
    },
    getTaskData(id) {
      axios.get(`${apiHost}/repair/a/wechat/api/rprorder/getRprOrder;JSESSIONID=${sessionId}`, {
        params: {
          procInsId: id
        }
      }).then(res => {
        const data = detailInfoFormat(res.data.body)
        const { department, content, title, location, issue, act, user, id } = data
        this.id = id
        this.dataId = res.data.body.rprOrder.id
        this.schoolArea = department.school
        this.area = department.area
        this.title = title
        this.description = content
        this.location = location
        this.attachment = imgUrl(issue)
        this.fj1 = imgUrl(issue)
        this.act = act
        this.user = {
          name: user.name,
          loginName: user.userNo,
          office: {
            name: user.depart.text,
            id: user.depart.value
          },
          id: user.id,
          mobile: user.phone
        }
        this.getClassGroup(department.area.value)

        if(data.assignClass.classGroupId) {
          this.getMemberGroup(data.assignClass.classGroupId)
        }
        this.assignClass = {
          text: data.assignClass.className,
          value: data.assignClass.id,
          classid: data.assignClass.classId,
          classGroupId: data.assignClass.classGroupId
        }
        this.leader = {
          name: data.assignClass.leader,
          userNo: data.assignClass.LeaderNo,
          classgroup: {
            id: data.assignClass.classGroupId
          }
        }
        this.assignMember = {
          text: data.worker.name,
          value: data.worker.userNo,
          id: data.worker.id,
          phone: data.worker.phone,
        }
      }).catch(err => {
        console.log(err)
      })
    },
    onBack() {
      this.$router.back(-1)
    }
  }
}
</script>
<style lang="scss">
  .form {
    margin: 8px 0;
  }
  .btn-group {
    margin: 8px 0;
    display: flex;
    .van-button {
      margin: 8px;
      flex: 1;
    }
  }
  .van-field__control[readonly] {
    color: #999;
  }
  .van-field__error-message {
    font-size: 14px;
  }
</style>