import { apiHost } from '../config'
const detailInfoFormat = data => {
  const { rprOrder } = data;
  const {
    bxr: { id: idNo, name },
    campus = {},
    cate = {},
    content,
    mobile,
    rpaNo,
    rpaTtile,
    address,
    rpaCls = {},
    leader,
    fj,
    depart = {},
    procInsSts,
    rpaUser = {},
    act,
    userNo,
    classGroupId,
    rpaLeaderNo,
    rpaRepairfj
  } = rprOrder
  return {
    id: rpaNo,
    user: {
      id: idNo,
      name,
      phone: mobile,
      userNo,
      loginName: userNo,
      depart: {
        text: depart.name,
        value: depart.id
      }
    },
    department: {
      school: {
        text: campus.name,
        value: campus.id
      },
      area: {
        text: cate.name,
        value: cate.id
      }
    },
    content,
    title: rpaTtile,
    issue: formatAttachment(fj),
    location: address,
    assignClass: {
      className: rpaCls.classname,
      leader,
      LeaderNo: rpaLeaderNo,
      classId: rpaCls.classid,
      id: rpaCls.id,
      classGroupId,
    },
    procIns: {
      status: procInsSts
    },
    worker: {
      name: rpaUser.userName,
      phone: rpaUser.phone,
      userNo: rpaUser.userNo,
      id: rpaUser.id,
      attachment: formatAttachment(rpaRepairfj)
    },
    act: {
      taskId: act.taskId,
      procDefId: act.procDefId,
      procDefKey: act.procDefKey,
      procInsId: act.procInsId,
      taskDefKey: act.taskDefKey,
      taskName: act.taskName
    }
  }
}

const formatAttachment = (files) => {
  if (!files) {
    return []
  }
  const fileArr = files.split('|').map(item => ({ url: item }));
  return imgUrl(fileArr);
}

const imgUrl = (imgs) => {
  return imgs.map(item => {
    if (item.url.includes('http')) {
      return item
    }
    return ({...item, url: apiHost + item.url })
  })
}

export {
  detailInfoFormat,
  imgUrl
}
